import React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import Grid from '@mui/material/Grid'
import  CssBaseline  from '@mui/material/CssBaseline';
import Typography from "@mui/material/Typography"

function Experiance({changeWorkYear, work, changeWork, submitWork}) {

    return(
        <CssBaseline>
            <Box component="form" noValidate autoComplete="off" onSubmit={(event)=>submitWork(event)}>
                <Grid container direction="column" alignItems="center" justifyContent="center" spacing={1} width="70%" margin="0 auto" >
                <Typography marginTop={5} variant='h3' textAlign="center">Work Experiance</Typography>
                    <Grid item width="100%">
                        <TextField
                            //error={education.school ? false : true}
                            id="outlined-required"
                            type='text'
                            name='company'
                            placeholder='Company'
                            value={work.company}
                            onChange={(event) => changeWork(event)}
                            //helperText={education.school ? "" : "Please Fill The Input"}
                            required
                            fullWidth
                            color="secondary"
                            focused 
                            
                        />
                    </Grid>
                    <Grid item width="100%">
                        <TextField
                            //error={education.subject ? false : true}
                            id="outlined-required"
                            type='text'
                            name='role'
                            placeholder='Role'
                            value={work.role}
                            onChange={(event) => changeWork(event)}
                            //helperText={education.subject ? "" : "Please Fill The Input"}
                            required
                            fullWidth
                            color="secondary"
                            focused 
                        />         
                    </Grid>
                    <Grid item width="100%">
                        <Grid container direction="row" spacing={1} justifyContent="center" alignItems="center">
                            <Grid item md={6} justifyContent="center">
                                <LocalizationProvider sx={{width:'100%'}}  dateAdapter={AdapterDateFns}>
                                    <DatePicker  
                                        //error="false"                           
                                        label="From"
                                        value={work.from}
                                        openTo="year"
                                        views={["year", "month"]}
                                        onChange={(event) => changeWorkYear(event, 2)}
                                        renderInput={(params) => <TextField {...params} />}
                                        fullWidth
                                        color="secondary"
                                        focused 
                                    />
                                </LocalizationProvider> 
                            </Grid>
                            <Grid item md={6} justifyContent="center">
                                <LocalizationProvider  dateAdapter={AdapterDateFns}>
                                    <DatePicker
                                        //error="false"
                                        label="To"
                                        value={work.to}
                                        openTo="year"
                                        views={["year", "month"]}
                                        onChange={(event) => changeWorkYear(event, 3)}
                                        renderInput={(params) => <TextField {...params} />}
                                        fullWidth
                                        color="secondary"
                                        focused
                                        
                                    />
                            </LocalizationProvider> 
                            </Grid>
                        </Grid>
                    </Grid>
                    <Button sx={{marginTop:"1rem"}} fullWidth color="secondary" size="medium" type="submit" variant="contained">Add</Button>    
                </Grid>
            </Box>
        </CssBaseline>    
           
    )
}
export default Experiance