import { useState } from "react"
import Header from "./components/Header"
import ContactInfo from "./components/Form/Contact"
import Education from "./components/Form/Education"
import Experiance from "./components/Form/Experiance"
import Example from "./components/Preview/Print"
import Skills from "./components/Form/Skills"
import Interest from "./components/Form/Interest"
import Switch from "./components/Switch"
import Grid from '@mui/material/Grid'
function App() {
  const [contact, setContact] = useState({firstname:'', secondname:'', email:'', phone:'', adress:'', photo:""})
  const [educational, setEducational] = useState([])
  const [education, setEducation] = useState({school:'', subject:'', from:'', to:''})
  const [experiance, setExperiance] = useState([])
  const [work, setWork] = useState({company:'', role:'', from:'', to:''})
  const [skills, setSkills] = useState([])
  const [skill, setSkill] = useState('')
  const [interests, setInterests] = useState([])
  const [interest, setInterest] = useState('')
  const [isPreview, setIsPreview] = useState(false)
  const submitEducation = (event) => {
    event.preventDefault()
    if(Object.values(education).every((item) => item !== "")){
      setEducational([...educational, education])
      setEducation({school:'', subject:'', from:'', to:''})
    }
    return
  }
  const submitWork = (event) => {
    event.preventDefault()
    if(Object.values(work).every((item) => item !== "")){
      setExperiance([experiance, work])
      setWork({company:'', role:'', from:'', to:''})
    }    
    return
  }
  const changeContact = (event) => {
    const name = event.target.name
    const value = event.target.value
    setContact({...contact, [name] : value}) 
  }

  const changeEducation = (event) => {
    const name = event.target.name
    const value = event.target.value
    setEducation({...education, [name] : value})
    
  }
  const changeWork = (event) => {
    const name = event.target.name
    const value = event.target.value
    setWork({...work, [name] : value})    
  }
  const changePhone = (event) => {
    setContact({...contact, phone : event})
  }
  const changeEducationYear = (date, index) => {
    const value = date
    setEducation({...education, [Object.keys(education)[index]] : value})
  }
  const changeWorkYear = (date, index) => {
    const value = date
    setWork({...work, [Object.keys(work)[index]] : value})
  }
  const changeSkill = (event) => {
    setSkill(event.target.value)
  }
  const clickSkill = () => {
    setSkills([skills, skill])
    setSkill('')
  }
  const changeInterest = (event) => {
    setInterest(event.target.value)
  }
  const clickInterest = () => {
    setInterests([interests, interest])
    setInterest('')
  }
  const changePreview = () => {
      setIsPreview(!isPreview)
  }
  if(isPreview){
    return(
      <>
    <Header />
    <Grid sx={{borderLeft:"2px solid black",borderRight:"2px solid black", width:'70%', margin:'0 auto', marginTop:'-2rem', backgroundColor:'cornsilk'}} className="fade">
      <Switch isPreview={isPreview} changePreview={changePreview} />
      <Example contact={contact} educational={educational} experiance={experiance} skills={skills} interests={interests} /> 
    </Grid>       
      </>

    )
 
  }
  return (
    <>
      <Header />
      <Grid sx={{borderLeft:"2px solid black",borderRight:"2px solid black", width:'70%', margin:'0 auto', marginTop:'-2rem', backgroundColor:'cornsilk'}} className="fade">
        <Switch isPreview={isPreview} changePreview={changePreview} />
        <ContactInfo contact={contact} changeContact={changeContact} changePhone={changePhone} />
        <Education changeEducationYear={changeEducationYear} education={education} changeEducation={changeEducation} submitEducation={submitEducation}/>
        <Experiance changeWorkYear={changeWorkYear} work={work} changeWork={changeWork} submitWork={submitWork}/>
        <Skills skill={skill} changeSkill={changeSkill} clickSkill={clickSkill} />
        <Interest interest={interest} changeInterest={changeInterest} clickInterest={clickInterest} />
      </Grid>
    </>
    
  )
}

export default App
