import React from "react";
import CssBaseline from '@mui/material/CssBaseline'
import Typography from '@mui/material/Typography'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button'
import Grid from '@mui/material/Grid'
export default function Interest({interest, changeInterest, clickInterest}) {
    return(
<>
    <CssBaseline>
            <Typography marginTop={5} textAlign="center" variant="h3">Interests</Typography>
            <Grid width="70%" margin="0 auto" >
                <TextField 
                    type="text"
                    name="interest"
                    placeholder="Interest"
                    value={interest}
                    onChange={(event) => changeInterest(event)}
                    color="secondary"
                    focused
                />
            <Button sx={{marginTop:"1rem", marginBottom:'3rem'}} type="button" color="secondary" variant="contained" fullWidth onClick={() => clickInterest()}>Add</Button>
        </Grid>
    </CssBaseline>
</>
    )
}