import React from "react";
import CssBaseline from '@mui/material/CssBaseline'
import Typography from '@mui/material/Typography'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button'
import Grid from '@mui/material/Grid'
export default function Skills({skill, changeSkill, clickSkill}) {
    return(
        <CssBaseline>
            <Typography marginTop={5} textAlign="center" variant="h3">Skills</Typography>
            <Grid width="70%" margin="0 auto" >
                <TextField 
                    type="text"
                    name="skill"
                    placeholder="Skill"                    
                    value={skill}
                    onChange={(event) => changeSkill(event)}
                    color="secondary"
                    focused
                />
            <Button sx={{marginTop:"1rem"}} type="button" color="secondary" variant="contained" fullWidth onClick={() => clickSkill()}>Add</Button>
        </Grid>
        </CssBaseline>
    )
} 